export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Name");
  head.push("Available status");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.maNganhDaoTao);
  row.push(data.ten);
  row.push(data.isDeleted ? "Deleted" : "Available");
  return row;
}
